import ReactGA from "react-ga";
import React, { createContext } from "react";

export const analyticsContext = createContext({ event: ({}) => {} });

const analyticsProvider = (props) => {
  ReactGA.initialize(process.env.REACT_APP_GA);

  const analyticsEvent = (action, category) => {
    ReactGA.event({ action: action, category: category });
  };

  return (
    <analyticsContext.Provider value={{ event: analyticsEvent }}>
      {props.children}
    </analyticsContext.Provider>
  );
};

export default analyticsProvider;
